import style from './topsites.scss'
import React, { Component } from 'react'
import PropTypes from 'prop-types'
// import OptIn from './optIn'
import NewtabTopsitesItem from './item'

const TOP_SITES_COUNT = 8

export default class NewtabTopsitesList extends Component {
  render() {
    return (
      <div className={style.section}>
        {this.props.topSitesVerbose && (
          <div className={style.sectionTitle}>Most Visited Sites</div>
        )}
        <ul className={style.list}>
          {this.props.list
            .slice(0, TOP_SITES_COUNT)
            .map((data, index) => (
              <NewtabTopsitesItem
                darkModeActive={this.props.darkModeActive}
                topSitesVerbose={this.props.topSitesVerbose}
                key={index}
                item={data}
              />
            ))}
        </ul>
      </div>
    )
  }
}

NewtabTopsitesList.propTypes = {
  list: PropTypes.array
}

