import * as Icon from '../../_common/components/icons'
import { getImageCacheUrl } from '../../../state/_common/helpers'

import style from './topsites.scss'
import React, { Component } from 'react'
import PropTypes from 'prop-types'

import classNames from 'classnames/bind'
const cx = classNames.bind(style)

export default class Media extends Component {
  constructor(props) {
    super(props)
    this.state = {
      image: getImageCacheUrl(`https://logo.clearbit.com/${props.domain}`),
      isValid: false
    }
  }

  testImage() {
    const testImage = new Image()
    testImage.onload = () => {
      this.setState({ isValid: true })
    }
    testImage.onerror = () => {
      this.setState({ isValid: false })
    }
    testImage.src = this.state.image
  }

  componentWillMount() {
    this.testImage()
  }

  get imageMarkup() {
    const mediaClass = cx({
      image: true,
      darkMode: this.props.darkModeActive
    })

    return this.state.isValid ? (
      <img
        className={mediaClass}
        src={this.state.image}
        alt={this.props.domain}
      />
    ) : (
      Icon.DefaultSite({
        width: '64px',
        height: '64px'
      })
    )
  }

  render() {
    return this.imageMarkup
  }
}

Media.propTypes = {
  domain: PropTypes.string
}



