import style from './recommendations.scss'
import React, { Component } from 'react'
import PropTypes from 'prop-types'
import * as Icon from '../../_common/components/icons'
import classNames from 'classnames/bind'
// import * as Icon from '../../_common/components/icons'
import NewtabRecommendationItem from './item'
import Skeleton from './skeleton'

const cx = classNames.bind(style)

export default class NewtabRecommendationList extends Component {
  listItems(list) {
    return Object.keys(list)
      .slice(0, 3)
      .map(x => list[x])
      .sort((a, b) => parseInt(a.index, 10) - parseInt(b.index, 10))
  }

  hasErrors() {
    return this.props.errors && this.props.errors.recs
  }

  render() {
    const listClass = cx({
      list: true,
      darkMode: this.props.darkModeActive
    })

    const list =
      !!Object.keys(this.props.list).length && !this.hasErrors()
        ? this.props.list
        : [
            { id: 0, skeleton: true },
            { id: 1, skeleton: true },
            { id: 2, skeleton: true }
          ]

    return (
      <div className={style.trendingStories}>
        <h4 className={style.sectionTitle}>
          <a
            className={style.titleLink}
            href="https://getpocket.com/explore/trending?src=chrome_new_tab">
            Trending Stories
          </a>
          <div className={style.logo}>
            <a
              href="https://www.getpocket.com"
              onClick={this.props.analyticsClickPocket}>
              {Icon.PocketLogoType({ width: 'auto', height: '20px' })}
            </a>
          </div>
        </h4>
        {this.hasErrors() && (
          <div className={style.error}>
            {Icon.Disconnected({
              width: '50px',
              height: 'auto',
              marginRight: '40px'
            })}
            <div className={style.errorBlock}>
              <div className={style.errorCopy}>
                It appears there was an issue retrieving trending stories from
                the server. We will{' '}
                <button onClick={this.props.refresh} className={style.retry}>
                  try again
                </button>{' '}
                shortly.
              </div>
            </div>
          </div>
        )}

        {!this.hasErrors() && (
          <ul className={listClass}>
            {this.listItems(list).map((data, index) => {
              return data.skeleton ? (
                <Skeleton
                  key={data.id}
                  darkModeActive={this.props.darkModeActive}
                />
              ) : (
                <NewtabRecommendationItem
                  darkModeActive={this.props.darkModeActive}
                  analyticsClickRec={this.props.analyticsClickRec}
                  saveRec={this.props.saveRec}
                  removeRec={this.props.removeRec}
                  index={index}
                  key={data.id}
                  item={data}
                />
              )
            })}
          </ul>
        )}
      </div>
    )
  }
}

NewtabRecommendationList.propTypes = {
  saveRec: PropTypes.func,
  list: PropTypes.object
}



