import style from './topsites.scss'
import React, { Component } from 'react'
import PropTypes from 'prop-types'

import { domainForUrl } from '../../../state/_common/utilities'
import Tooltip, { setTooltip, unsetTooltip } from './tooltip' //
import Media from './media'

import classNames from 'classnames/bind'
const cx = classNames.bind(style)

export default class NewtabTopicItem extends Component {
  constructor(props) {
    super(props)
    this.state = { positions: false }

    this.setTooltip = setTooltip.bind(this)
    this.unsetTooltip = unsetTooltip.bind(this)
  }

  render() {
    let item = this.props.item

    const anchorClass = cx({
      darkMode: this.props.darkModeActive
    })

    const topSiteClass = cx({
      topsite: true,
      verbose: this.props.topSitesVerbose
    })

    return (
      <li
        className={topSiteClass}
        onMouseEnter={event => {
          this.setTooltip(event.currentTarget)
        }}
        onMouseLeave={event => {
          this.unsetTooltip(event.currentTarget)
        }}>
        <a href={item.url} className={anchorClass}>
          <Media
            domain={domainForUrl(item.url)}
            darkModeActive={this.props.darkModeActive}
          />
          <div className={style.description}>{item.title}</div>
        </a>

        {!this.props.topSitesVerbose && (
          <Tooltip
            darkModeActive={this.props.darkModeActive}
            positions={this.state.positions}
            copy={item.title}
          />
        )}
      </li>
    )
  }
}

NewtabTopicItem.propTypes = {
  item: PropTypes.object,
  setTooltip: PropTypes.func,
  unsetTooltip: PropTypes.func
}



