import { openUrl } from '../../../state/_common/interface'
import {
  isValidAddress,
  createValidUrl
} from '../../../state/_common/utilities'
import style from './search.scss'
import React, { Component } from 'react'
import PropTypes from 'prop-types'

import classNames from 'classnames/bind'
const cx = classNames.bind(style)

const ENTER = 13

export default class NewtabSearch extends Component {
  handleKeyPress = event => {
    if (event.charCode === ENTER) {
      event.preventDefault()

      if (isValidAddress(this.input.value)) {
        return openUrl(createValidUrl(this.input.value))
      }

      openUrl(
        'https://www.google.com/search?q=' +
          encodeURI(this.input.value.replace(/\s/g, '+'))
      )
    }
  }

  render() {
    const inputClass = cx({
      input: true,
      darkMode: this.props.darkModeActive
    })

    return (
      <div className={style.search}>
        <input
          type="text"
          className={inputClass}
          ref={input => (this.input = input)}
          onKeyPress={this.handleKeyPress}
          placeholder="Search Google or Type URL"
        />
      </div>
    )
  }
}

NewtabSearch.propTypes = {
  list: PropTypes.array
}



