import style from './newtab.scss' // Import style
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { mapStateToProps, mapDispatchToProps } from '../../state/connect'
import { getVersion } from '../../state/_common/interface'

import Infopanel from './infopanel/infopanel'
import NewtabSettings from './settings/settings'
import NewtabSearch from './search/search'
import NewtabTopsitesList from './topsites/list'
import NewtabRecommendationList from './recommendations/list'
import NewtabTopicList from './topics/list'
import BookmarksBar from './bookmarksbar/bookmarksbar'

import classNames from 'classnames/bind'
const cx = classNames.bind(style)

class App extends Component {
  constructor(props) {
    super(props)
    this.state = { barActive: false }
  }

  componentDidMount() {
    this.props.newTabOpened()
    document.body.classList.toggle('darkMode', this.props.newtab.darkModeActive)
  }
  componentWillReceiveProps(nextProps) {
    document.body.classList.toggle('darkMode', nextProps.newtab.darkModeActive)
  }
  componentWillUnmount() {
    document.body.classList.remove('darkMode')
  }

  barActivate = () => this.setState({ barActive: true })

  barDeactivate = () => {
    this.setState({ barActive: false })
    this.props.clearBookmarksNode()
  }

  render() {
    const wrapperClass = cx({
      container: true,
      darkMode: this.props.newtab.darkModeActive,
      obscure:
        (this.state.barActive && this.props.newtab.bookmarksAccess) ||
        this.props.newtab.infoPanelType
    })

    return (
      <div>
        {this.props.newtab.bookmarksActive &&
          this.props.newtab.bookmarksAccess &&
          this.props.newtab.bookmarks && (
            <BookmarksBar
              infoPanelType={this.props.newtab.infoPanelType}
              barActive={this.state.barActive}
              barActivate={this.barActivate}
              barDeactivate={this.barDeactivate}
              bookmarks={this.props.newtab.bookmarks}
              getBookmarks={this.props.getBookmarks}
              activateOverflow={this.props.activateOverflow}
              openBookmarksNode={this.props.openBookmarksNode}
              closeBookmarksNode={this.props.closeBookmarksNode}
              clearBookmarksNode={this.props.clearBookmarksNode}
              toggleBookmarks={this.props.toggleBookmarks}
              darkModeActive={this.props.newtab.darkModeActive}
            />
          )}

        {!this.props.newtab.infoPanelType &&
          !this.state.barActive && (
            <NewtabSettings
              toggleBookmarks={this.props.toggleBookmarks}
              bookmarksActive={this.props.newtab.bookmarksActive}
              bookmarksAccess={this.props.newtab.bookmarksAccess}
              searchBarActive={this.props.newtab.searchBarActive}
              toggleSearchBar={this.props.toggleSearchBar}
              topSitesActive={this.props.newtab.topSitesActive}
              toggleTopSites={this.props.toggleTopSites}
              toggleVerboseTopSites={this.props.toggleVerboseTopSites}
              topSitesVerbose={this.props.newtab.topSitesVerbose}
              darkModeActive={this.props.newtab.darkModeActive}
              toggleDarkMode={this.props.toggleDarkMode}
              removePermissions={this.props.removePermissions}
            />
          )}

        {this.props.newtab.infoPanelType && (
          <Infopanel
            version={getVersion()}
            featuresSelected={this.props.featuresSelected}
            infoPanelType={this.props.newtab.infoPanelType}
            bookmarksActive={this.props.newtab.bookmarksActive}
            searchBarActive={this.props.newtab.searchBarActive}
            topSitesActive={this.props.newtab.topSitesActive}
            darkModeActive={this.props.newtab.darkModeActive}
            toggleDarkMode={this.props.toggleDarkMode}
          />
        )}

        <div className={wrapperClass}>
          <header className={style.header}>
            {this.props.newtab.searchBarActive &&
              !this.props.newtab.infoPanelType && (
                <NewtabSearch
                  darkModeActive={this.props.newtab.darkModeActive}
                />
              )}

            {this.props.newtab.topSitesActive &&
              this.props.newtab.topSitesAccess &&
              this.props.newtab.topSites && (
                <NewtabTopsitesList
                  analyticsClickTopSite={this.props.analyticsClickTopSite}
                  list={this.props.newtab.topSites}
                  getTopSites={this.props.getTopSites}
                  toggleTopSites={this.props.toggleTopSites}
                  toggleSearchBar={this.props.toggleTopSites}
                  topSitesVerbose={this.props.newtab.topSitesVerbose}
                  darkModeActive={this.props.newtab.darkModeActive}
                />
              )}
          </header>

          <NewtabRecommendationList
            analyticsClickPocket={this.props.analyticsClickPocket}
            analyticsClickRec={this.props.analyticsClickRec}
            refresh={this.props.newTabRefresh}
            list={this.props.newtab.recs}
            errors={this.props.newtab.errors}
            saveRec={this.props.saveGlobalRec}
            removeRec={this.props.removeGlobalRec}
            darkModeActive={this.props.newtab.darkModeActive}
          />

          {!!this.props.newtab.topics.length && (
            <NewtabTopicList
              analyticsClickViewMore={this.props.analyticsClickViewMore}
              analyticsClickTrending={this.props.analyticsClickTrending}
              list={this.props.newtab.topics}
            />
          )}
        </div>
      </div>
    )
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App)



