import style from './infopanel.scss'
import React, { Component } from 'react'
import * as Icon from '../../_common/components/icons'
// import { randomEntry } from '../../../state/_common/utilities'

import classNames from 'classnames/bind'
const cx = classNames.bind(style)

export default class OptIn extends Component {
  constructor(props) {
    super(props)
    this.state = {
      active: false,
      darkmode: props.darkModeActive,
      bookmarks: props.bookmarksActive,
      search: props.searchBarActive,
      topsites: props.topSitesActive,
      greatDebate: false
    }
  }

  //Handlers
  onKeyUp = event => {
    if (event.keyCode === 39)
      this.setState({
        greatDebate: false
      })
    if (event.keyCode === 37)
      this.setState({
        greatDebate: true
      })
  }

  componentDidMount() {
    document.addEventListener('keyup', this.onKeyUp)
  }

  componentWillUnmount() {
    document.removeEventListener('keyup', this.onKeyUp)
  }

  get header() {
    return this.props.infoPanelType === 'update'
      ? `Pocket New Tab updated to version ${this.props.version}`
      : 'Welcome to the Pocket New Tab'
  }

  get contentCopy() {
    return this.props.infoPanelType === 'update' ? (
      <span>
        You can now see your <strong>Bookmarks Bar</strong> on the new tab page!
        We’ve also fixed some bugs and and added general stability improvements.
      </span>
    ) : (
      <span>
        To get started, customize what you’d like to see on your new tab. Note:
        Some features require special permissions from Chrome, but don’t worry,
        information accessed with these permissions will not be sent to Pocket
        or shared with anyone.
      </span>
    )
  }

  setActiveState = newstate => {
    this.setState({ activeState: newstate })
  }

  toggleDarkMode = () => this.setState({ darkmode: !this.state.darkmode })
  toggleBookmarks = () => this.setState({ bookmarks: !this.state.bookmarks })
  toggleSearch = () => this.setState({ search: !this.state.search })
  toggleTopsites = () => this.setState({ topsites: !this.state.topsites })

  finalizeSetup = () => {
    this.props.featuresSelected({
      bookmarks: this.state.bookmarks,
      search: this.state.search,
      topSites: this.state.topsites,
      darkMode: this.state.darkmode
    })
  }

  render() {
    const wrapperClass = cx({
      optIn: true,
      darkModeSet: this.props.darkModeActive
    })

    const toggleBookmarkClass = cx({
      toggle: true,
      active: this.state.bookmarks
    })

    const toggleTopSitesClass = cx({
      toggle: true,
      active: this.state.topsites
    })

    const toggleSearchBarClass = cx({
      toggle: true,
      active: this.state.search
    })

    const toggleDarkModeClass = cx({
      toggle: true,
      active: this.state.darkmode
    })

    const demoClass = cx({
      demo: true,
      darkMode: this.state.darkmode
    })

    const demoBookmarksClass = cx({
      demoBookmarks: true,
      active: this.state.bookmarks
    })

    const demoSearchBarClass = cx({
      demoSearchBar: true,
      active: this.state.search
    })

    const demoTopSitesClass = cx({
      demoTopSites: true,
      active: this.state.topsites
    })

    const mainClass = cx({
      main: true,
      flipped: this.state.greatDebate
    })

    return (
      <div className={wrapperClass}>
        <div className={style.rainbow} />
        <div className={style.header}>
          <div className={style.headerTitle}>
            {Icon.PocketLogo({
              width: '1em',
              height: '1em',
              marginRight: '10px',
              marginTop: '2px',
              verticalAlign: 'text-top'
            })}
            {this.header}
          </div>
        </div>
        <div className={style.intro}>
          <div className={style.introCopy}>{this.contentCopy}</div>
        </div>
        <div className={mainClass}>
          <div className={demoClass}>
            <div className={demoBookmarksClass} />
            <div className={demoSearchBarClass} />
            <div className={demoTopSitesClass}>
              <div className={style.demoTopSite} />
              <div className={style.demoTopSite} />
              <div className={style.demoTopSite} />
              <div className={style.demoTopSite} />
              <div className={style.demoTopSite} />
              <div className={style.demoTopSite} />
              <div className={style.demoTopSite} />
              <div className={style.demoTopSite} />
            </div>
            <div className={style.demoRecs}>
              <div className={style.demoRec}>
                <div className={style.imageBlock} />
              </div>
              <div className={style.demoRec}>
                <div className={style.imageBlock} />
              </div>
              <div className={style.demoRec}>
                <div className={style.imageBlock} />
              </div>
            </div>
          </div>

          <div className={style.features}>
            <div className={style.selectors}>
              <div className={style.featureHeader}>Customize Your New Tab </div>
              <div className={style.section} onClick={this.toggleDarkMode}>
                <div className={style.label}>Dark Theme</div>
                <div className={style.choice}>
                  <button className={toggleDarkModeClass} />
                </div>
              </div>
              <div className={style.section} onClick={this.toggleBookmarks}>
                <div className={style.label}>Bookmarks Bar</div>
                <div className={style.choice}>
                  <button className={toggleBookmarkClass} />
                </div>
              </div>
              <div className={style.section} onClick={this.toggleSearch}>
                <div className={style.label}>Search Bar</div>
                <div className={style.choice}>
                  <button className={toggleSearchBarClass} />
                </div>
              </div>
              <div className={style.section} onClick={this.toggleTopsites}>
                <div className={style.label}>Most Visited Sites</div>
                <div className={style.choice}>
                  <button className={toggleTopSitesClass} />
                </div>
              </div>
            </div>
            <div className={style.actions}>
              <button className={style.buttonYes} onClick={this.finalizeSetup}>
                See Your New Tab!
              </button>
            </div>
          </div>
        </div>
      </div>
    )
  }
}



