import style from './skeleton.scss'
import React, { Component } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import * as Icon from '../../_common/components/icons'

const cx = classNames.bind(style)

export default class Skeleton extends Component {
  constructor(props) {
    super(props)
    this.state = { active: false }
  }

  hoverOn(activeTitle) {
    this.setState({
      active: true,
      activeTitle: activeTitle
    })
  }

  hoverOff() {
    this.setState({
      active: false,
      activeTitle: false
    })
  }

  render() {
    let itemClass = cx({
      skeleton: true,
      active: this.state.active,
      darkMode: this.props.darkModeActive
    })

    return (
      <li className={itemClass}>
        <a
          className={style.tileLink}
          onMouseOver={() => this.hoverOn(true)}
          onMouseOut={() => this.hoverOff()}
          rel="noopener noreferrer">
          {' '}
        </a>
        <div className={style.image}>{Icon.DefaultSite(80, 80)}</div>
        <div className={style.copy}>
          <div className={style.copyBlocks} />
        </div>
        <div className={style.action}>
          <div className={style.actionBlock} />
        </div>
      </li>
    )
  }
}

Skeleton.propTypes = {
  item: PropTypes.object
}



