import style from './settings.scss'
import React, { Component } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import * as Icon from '../../_common/components/icons'

const cx = classNames.bind(style)

export default class NewtabSettings extends Component {
  constructor(props) {
    super(props)
    this.state = { active: false }
  }

  hoverOn() {
    clearTimeout(this.hoverTimer)
    this.setState({ active: true })
  }

  hoverOff() {
    this.hoverTimer = setTimeout(() => {
      this.setState({ active: false })
    }, 250)
  }

  togglePanel() {
    this.setState({
      active: !this.state.active
    })
  }
  removePermissions() {}

  render() {
    const wrapperClass = cx({
      settings: true,
      darkMode: this.props.darkModeActive,
      bookmarksActive: this.props.bookmarksActive && this.props.bookmarksAccess
    })

    const panelClass = cx({
      panel: true,
      active: this.state.active
    })

    const toggleBookmarkClass = cx({
      toggle: true,
      active: this.props.bookmarksActive
    })

    const toggleTopSitesClass = cx({
      toggle: true,
      active: this.props.topSitesActive
    })

    // const toggleVerboseTopSitesClass = cx({
    //     toggle: true,
    //     active: this.props.topSitesVerbose
    // })

    const toggleSearchBarClass = cx({
      toggle: true,
      active: this.props.searchBarActive
    })

    const toggleDarkModeClass = cx({
      toggle: true,
      active: this.props.darkModeActive
    })

    return (
      <div className={wrapperClass}>
        <button
          title="Pocket New Tab Preferences"
          className={style.trigger}
          onMouseOver={() => this.hoverOn()}
          onMouseOut={() => this.hoverOff()}>
          {Icon.Cog({ width: '16px', height: '16px', marginRight: '0' })}
        </button>
        <div className={style.options}>
          <div
            className={panelClass}
            onMouseOver={() => this.hoverOn()}
            onMouseOut={() => this.hoverOff()}>
            <div className={style.header}>
              {Icon.PocketLogo({
                width: '16px',
                height: '16px',
                marginRight: '3px',
                marginTop: '2px',
                verticalAlign: 'text-top'
              })}{' '}
              Pocket New Tab Preferences
              <div className={style.headerCopy}>
                Customize your new tab. <br />
                <br />
                Some options require special permissions in Chrome. Information
                accessed with these permissions will never be sent to Pocket or
                shared with anyone.
                <a
                  className={style.headerLink}
                  href="https://help.getpocket.com/article/1141-using-the-pocket-new-tab-extension-in-chrome">
                  Learn More{' '}
                  {Icon.ArrowRight({
                    width: '6px',
                    height: '6px',
                    marginRight: '0',
                    marginBottom: '1px',
                    verticalAlign: 'baseline'
                  })}
                </a>
              </div>
            </div>

            <div className={style.section}>
              <div className={style.title}>Dark Theme</div>
              <div className={style.choice}>
                <button
                  className={toggleDarkModeClass}
                  onClick={this.props.toggleDarkMode}
                />
              </div>
            </div>

            <div className={style.section}>
              <div className={style.title}>Bookmarks Bar</div>
              <div className={style.choice}>
                <button
                  className={toggleBookmarkClass}
                  onClick={this.props.toggleBookmarks}
                />
              </div>
            </div>

            <div className={style.section}>
              <div className={style.title}>Search Bar</div>
              <div className={style.choice}>
                <button
                  className={toggleSearchBarClass}
                  onClick={this.props.toggleSearchBar}
                />
              </div>
            </div>

            <div className={style.section}>
              <div className={style.title}>Most Visited Sites</div>
              <div className={style.choice}>
                <button
                  className={toggleTopSitesClass}
                  onClick={this.props.toggleTopSites}
                />
              </div>
            </div>

            <a
              className={style.feedback}
              href="https://www.surveymonkey.com/r/chromenewt">
              Let us know what you think{' '}
              {Icon.ArrowRight({
                width: '6px',
                height: '6px',
                marginRight: '0',
                marginBottom: '1px',
                verticalAlign: 'baseline'
              })}
            </a>
          </div>
        </div>
      </div>
    )
  }
}

NewtabSettings.propTypes = {
  trendingOn: PropTypes.bool
}

