import style from './topics.scss'
import React, { Component } from 'react'
import PropTypes from 'prop-types'
import * as Icon from '../../_common/components/icons'
import NewtabTopicItem from './item'

export default class NewtabTopicList extends Component {
  render() {
    return (
      <div className={style.section}>
        <div className={style.topics}>
          <div className={style.title}>
            <a
              className={style.titleLink}
              href="https://getpocket.com/explore?src=chrome_new_tab">
              Popular Topics:
            </a>
          </div>
          <ul className={style.list}>
            {this.props.list &&
              this.props.list.map((data, index) => (
                <NewtabTopicItem
                  analyticsClickTrending={this.props.analyticsClickTrending}
                  key={index}
                  index={index}
                  item={data}
                />
              ))}
          </ul>
        </div>
        <div className={style.viewMore}>
          <a
            className={style.viewMoreLink}
            onClick={this.props.analyticsClickViewMore}
            href="https://getpocket.com/explore/trending?src=chrome_new_tab">
            More Stories{' '}
            {Icon.ArrowRight({
              width: '6px',
              height: '6px',
              marginRight: '0',
              marginBottom: '1px',
              verticalAlign: 'baseline'
            })}
          </a>
        </div>
      </div>
    )
  }
}

NewtabTopicList.propTypes = {
  list: PropTypes.array
}



