import style from './bookmarksbar.scss'
import React, { Component } from 'react'
import PropTypes from 'prop-types'
// import OptInBookmarks from './optIn/optIn'
import BookmarksList from './bar/list'
import Submenu from './submenu/submenu'

import onClickOutside from 'react-onclickoutside'

import classNames from 'classnames/bind'
const cx = classNames.bind(style)

const ESCAPE = 27

class BookmarksBar extends Component {
  constructor(props) {
    super(props)
    this.state = { overflowList: [] }
  }

  // Lifecycle
  componentDidMount() {
    window.addEventListener('resize', this.eventThrottler)
    document.addEventListener('keyup', this.onKeyUp)
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.eventThrottler)
    document.removeEventListener('keyup', this.onKeyUp)
  }

  componentWillReceiveProps(nextProps) {
    if (!this.state.visibleList)
      this.setState({
        visibleList: nextProps.bookmarks.list
      })
    if (this.itemList) this.checkVisible(this.dimensions.width - 70)
  }

  componentDidUpdate() {
    if (this.props.bookmarks.list && !this.itemList) this.setItemList()
  }

  //Handlers
  onKeyUp = event => {
    if (event.keyCode === ESCAPE) this.handleClickOutside()
  }

  // Getters
  getItemPosition = item =>
    item.offsetLeft - item.scrollLeft + item.clientLeft + item.offsetWidth

  // Setters
  setItemList = () => {
    const nodeList = [...this.element.firstChild.childNodes]

    this.itemList = nodeList.filter(item => item.getAttribute).map(item => {
      return {
        id: item.getAttribute('data-id'),
        position: this.getItemPosition(item),
        width: item.offsetWidth
      }
    })
    this.updateDimensions()
  }

  updateDimensions = () => {
    if (this.props.barActive) this.props.barDeactivate()
    this.dimensions = this.element.getBoundingClientRect()
    this.checkVisible(this.dimensions.width - 70)
  }

  eventThrottler = () => {
    if (!this.eventTimeout) {
      this.eventTimeout = setTimeout(() => {
        this.updateDimensions()
        clearTimeout(this.eventTimeout)
        this.eventTimeout = null
      }, 10)
    }
  }

  handleClickOutside = () => {
    if (!this.props.barActive) return
    this.props.barDeactivate()
  }

  checkVisible = barWidth => {
    if (!this.itemList) return

    const overFlowIds = this.itemList
      .filter(item => barWidth < item.position)
      .map(item => item.id)

    const overflowList = this.props.bookmarks.list.filter(
      item => overFlowIds.indexOf(item.id) >= 0
    )

    const visibleList = this.props.bookmarks.list.filter(
      item => overFlowIds.indexOf(item.id) < 0
    )

    this.setState({ overflowList, visibleList, overFlowIds })
  }

  setSubmenuPositon = itemId => {
    if (itemId === -1) {
      const lastItem = this.itemList[this.state.visibleList.length - 1]
      const overFlowPosition = lastItem.position + lastItem.width + 33

      this.setState({
        submenuPosition: {
          right: `${this.dimensions.width - overFlowPosition}px`,
          left: 'auto'
        }
      })
    } else {
      const position = this.itemList.filter(item => itemId === item.id)
      const submenuPosition = position[0].position - position[0].width
      const rightSide = this.dimensions.width - submenuPosition < 320
      if (position.length)
        this.setState({
          submenuPosition: {
            left: rightSide ? 'auto' : `${submenuPosition}px`,
            right: rightSide
              ? `${this.dimensions.width -
                  submenuPosition -
                  position[0].width}px`
              : 'auto'
          }
        })
    }
  }

  openFolder = item => {
    if (item === -1) return this.openOverflow()

    // Was this opened from the overflow menu
    const isInOverflow = this.state.overFlowIds.indexOf(item.id) > 0

    this.props.barActivate()
    if (item.parentId === '1' && !isInOverflow) {
      this.setState({ barId: item.id })
      this.setSubmenuPositon(item.id)
      this.props.openBookmarksNode({ item, barId: item.id })
    } else {
      this.props.openBookmarksNode({ item, barId: this.state.barId })
    }
  }

  openOverflow = () => {
    this.props.barActivate()
    this.setState({ barId: -1 })
    this.setSubmenuPositon(-1)
    this.props.activateOverflow(this.state.overflowList)
  }

  closeFolder = () => {
    const parent = this.props.bookmarks.secondary.parent
    const closeToOverflow = parent.parentId === '1' && this.state.barId === -1
    if (closeToOverflow)
      return this.props.activateOverflow(this.state.overflowList)

    this.props.closeBookmarksNode({ barId: this.state.barId })
  }

  // Render
  render() {
    const barClass = cx({
      bar: true,
      darkMode: this.props.darkModeActive,
      obscure: this.props.infoPanelType
    })
    return (
      <div
        className={barClass}
        ref={element => {
          this.element = element
        }}>
        {this.state.visibleList && (
          <BookmarksList
            darkModeActive={this.props.darkModeActive}
            barActive={this.props.barActive}
            openFolder={this.openFolder}
            closeFolder={this.closeFolder}
            clearFolders={this.props.clearBookmarksNode}
            secondary={this.props.bookmarks.secondary}
            bookmarks={this.state.visibleList}
            hasOverflow={this.state.overflowList.length > 0}
          />
        )}

        {this.props.bookmarks.secondary &&
          this.props.bookmarks.secondary.list && (
            <Submenu
              barId={this.state.barId}
              darkModeActive={this.props.darkModeActive}
              items={this.props.bookmarks.secondary}
              openFolder={this.openFolder}
              position={this.state.submenuPosition}
              closeFolder={this.closeFolder}
            />
          )}
      </div>
    )
  }
}

BookmarksBar.propTypes = {
  saveRec: PropTypes.func,
  list: PropTypes.object
}

export default onClickOutside(BookmarksBar)

