import style from './bar.scss'
import React, { Component } from 'react'
import PropTypes from 'prop-types'
// import BookmarkItem from './item'
import { openUrl } from '../../../../state/_common/interface'
import * as Icon from '../../../_common/components/icons'
import { link, folder } from '../_components'

import classNames from 'classnames/bind'
const cx = classNames.bind(style)

export default class BookmarksList extends Component {
  handleMouseEnter = () => {
    if (this.props.barActive) this.props.openFolder(-1)
  }

  render() {
    const darkMode = this.props.darkModeActive
    const listClass = cx({
      list: true,
      darkMode: darkMode
    })

    return (
      <ul className={listClass}>
        <li className={style.bookmarkManager}>
          <button
            title="Bookmark Manager"
            className={style.bookmarkManagerLink}
            onClick={() => openUrl('chrome://bookmarks')}
            onMouseEnter={this.props.clearFolders}>
            {Icon.Bookmark({
              display: 'inline-block',
              width: '16px',
              height: '16px',
              marginRight: '0'
            })}
          </button>
        </li>
        {this.props.bookmarks.map(item => (
          <li className={style.bookmark} key={item.id} data-id={item.id}>
            {item.url
              ? link({ item, darkMode, hoverAction: this.props.clearFolders })
              : folder({
                  item,
                  darkMode,
                  actions: {
                    open: this.props.openFolder,
                    close: this.props.closeFolder,
                    hover: this.props.barActive ? this.props.openFolder : false
                  }
                })}
          </li>
        ))}

        {this.props.hasOverflow && (
          <li className={style.bookmarkOverflow}>
            <button
              title="Bookmarks Bar Overflow"
              className={style.buttonLink}
              onClick={() => this.props.openFolder(-1)}
              onMouseEnter={this.handleMouseEnter}>
              {Icon.ArrowRight({
                display: 'inline-block',
                width: '6px',
                height: '6px',
                marginRight: '0'
              })}
              {Icon.ArrowRight({
                display: 'inline-block',
                width: '6px',
                height: '6px',
                marginRight: '0',
                marginLeft: '0'
              })}
            </button>
          </li>
        )}
      </ul>
    )
  }
}

BookmarksList.propTypes = {
  list: PropTypes.array
}

