// SETUP ACTIONS
export function setupExtension(settings) {
  return { type: 'SETUP_EXTENSION', settings }
}

// BROWSER ACTIONS
export function tabChanged() {
  return { type: 'ACTIVE_TAB_CHANGED' }
}
export function tabUpdated() {
  return { type: 'ACTIVE_TAB_UPDATED' }
}
export function openPocket() {
  return { type: 'OPEN_POCKET' }
}

// SAVE ACTIONS
export function saveFromButton() {
  return { type: 'ITEM_SAVE_FROM_BUTTON' }
}
export function saveFromContext() {
  return { type: 'ITEM_SAVE_FROM_CONTEXT' }
}

// PANEL ACTIONS
export function saveItem() {
  return { type: 'ITEM_SAVE' }
}
export function archiveItem() {
  return { type: 'ITEM_ARCHIVE' }
}
export function removeItem() {
  return { type: 'ITEM_REMOVE' }
}

// TAG ACTIONS
export function getTags() {
  return { type: 'TAG_REQUEST' }
}
export function activateTag(data) {
  return { type: 'TAG_ACTIVATE', data }
}
export function addTag(tag) {
  return { type: 'TAG_ADD', tag }
}
export function deactivateTags(data) {
  return { type: 'TAG_DEACTIVATE', data }
}
export function removeTags(data) {
  return { type: 'TAG_REMOVE', data }
}

// RECOMMENDATION ACTIONS
export function getRecs() {
  return { type: 'ITEM_RECS_REQUEST' }
}
export function recieveRecs() {
  return { type: 'ITEM_RECS_SUCCESS' }
}

export function saveRec(data) {
  return { type: 'REC_SAVE_REQUEST', data }
}

// NEW TAB
export function newTabOpened() {
  return { type: 'NEWTAB_OPENED' }
}
export function newTabRefresh() {
  return { type: 'NEWTAB_OPENED' }
}
export function featuresSelected(data) {
  return { type: 'FEATURES_SELECTED', data }
}

export function getBookmarks() {
  return { type: 'BOOKMARKS_REQUEST' }
}
export function openBookmarksNode(data) {
  return { type: 'BOOKMARKS_NODE_OPEN', data }
}
export function closeBookmarksNode(data) {
  return { type: 'BOOKMARKS_NODE_CLOSE', data }
}
export function clearBookmarksNode() {
  return { type: 'BOOKMARKS_NODE_CLEAR' }
}
export function toggleBookmarks() {
  return { type: 'BOOKMARKS_TOGGLE' }
}
export function activateOverflow(data) {
  return { type: 'BOOKMARKS_OVERFLOW', data }
}

export function getTopSites() {
  return { type: 'TOP_SITE_REQUEST' }
}
export function toggleTopSites() {
  return { type: 'TOP_SITE_TOGGLE' }
}
export function toggleVerboseTopSites() {
  return { type: 'TOP_SITE_VERBOSE_TOGGLE' }
}
export function removePermissions() {
  return { type: 'TOP_SITE_REMOVE_PERMISSION_REQUEST' }
}

export function toggleSearchBar() {
  return { type: 'SEARCH_BAR_TOGGLE' }
}
export function toggleDarkMode() {
  return { type: 'DARK_MODE_TOGGLE' }
}

export function saveGlobalRec(data) {
  return { type: 'GLOBAL_REC_SAVE_REQUEST', data }
}
export function removeGlobalRec(data) {
  return { type: 'GLOBAL_REC_REMOVE_REQUEST', data }
}

// ERROR HANDLING
export function handleRequestError() {
  return { type: 'HANDLE_REQUEST_ERROR' }
}

// Analytics
export function analyticsUninstall() {
  return { type: 'ANALYTICS_UNINSTALL' }
}
export function analyticsClickPocket() {
  return { type: 'ANALYTICS_CLICK_POCKET' }
}
export function analyticsClickRec(data) {
  return { type: 'ANALYTICS_REC_CLICKED', data }
}
export function analyticsClickTopSite() {
  return { type: 'ANALYTICS_TOP_SITES_CLICK' }
}
export function analyticsClickTrending(data) {
  return { type: 'ANALYTICS_TRENDING_TOPIC_CLICK', data }
}
export function analyticsClickViewMore() {
  return { type: 'ANALYTICS_VIEW_MORE_CLICK' }
}



