import style from './topics.scss'
import React, { Component } from 'react'
import PropTypes from 'prop-types'

export default class NewtabTopicItem extends Component {
  render() {
    let item = this.props.item
    return (
      <li className={style.topic}>
        <a
          href={`${item.url}?src=pocketnewtab`}
          onClick={() =>
            this.props.analyticsClickTrending({ item, index: this.props.index })
          }>
          {item.name}
        </a>
      </li>
    )
  }
}

NewtabTopicItem.propTypes = {
  item: PropTypes.object
}



